import React, { useState } from "react";
import { useNavigate } from "react-router-dom";

function ConsentPage() {
  const navigate = useNavigate();
  const [allChecked, setAllChecked] = useState(false);
  const [checks, setChecks] = useState({
    terms: false,
    privacy: false,
    sensitive: false,
  });

  const [modalType, setModalType] = useState(null); // "terms" | "privacy" | "sensitive" | null

  const contentMap = {
    terms: `
1. 
   valoo            .

2.  
valoo            .

... 
`,
    privacy: `
[  ]
-  : , ,  
-  :    
-  :   
`,
    sensitive: `
[     ]
    ,        ,         .
`
  };

  const toggleCheck = (key) => {
    const updated = { ...checks, [key]: !checks[key] };
    setChecks(updated);
    setAllChecked(Object.values(updated).every(Boolean));
  };

  const handleAllCheck = () => {
    const newVal = !allChecked;
    setAllChecked(newVal);
    setChecks({
      terms: newVal,
      privacy: newVal,
      sensitive: newVal,
    });
  };

  const isRequiredAgreed = checks.terms;

  return (
    <div style={{ height: "100vh", backgroundColor: "#000", display: "flex", flexDirection: "column", justifyContent: "flex-end" }}>
      {/*   */}
      <div style={{
        backgroundColor: "#fff",
        borderTopLeftRadius: "1rem",
        borderTopRightRadius: "1rem",
        padding: "1.5rem",
        boxSizing: "border-box"
      }}>
        <h3 style={{ marginBottom: "0.5rem" }}>  .</h3>
        <p style={{ fontSize: "0.9rem", color: "#555", marginBottom: "1.5rem" }}>
               ,<br />
               .
        </p>

        <label style={checkboxStyle}>
          <input type="checkbox" checked={allChecked} onChange={handleAllCheck} />
          <strong style={{ marginLeft: "0.5rem" }}> </strong>
        </label>

        <hr />

        <label style={checkboxStyle}>
          <input type="checkbox" checked={checks.terms} onChange={() => toggleCheck("terms")} />
          <span style={{ marginLeft: "0.5rem" }}>()   </span>
          <span style={viewBtn} onClick={() => setModalType("terms")}></span>
        </label>

        <label style={checkboxStyle}>
          <input type="checkbox" checked={checks.privacy} onChange={() => toggleCheck("privacy")} />
          <span style={{ marginLeft: "0.5rem" }}>()   </span>
          <span style={viewBtn} onClick={() => setModalType("privacy")}></span>
        </label>

        <label style={checkboxStyle}>
          <input type="checkbox" checked={checks.sensitive} onChange={() => toggleCheck("sensitive")} />
          <span style={{ marginLeft: "0.5rem" }}>()     </span>
          <span style={viewBtn} onClick={() => setModalType("sensitive")}></span>
        </label>

        <button
          disabled={!isRequiredAgreed}
          onClick={() => navigate("/modeselection")}  //   navigate  
          style={{
            marginTop: "2rem",
            width: "100%",
            padding: "1rem",
            backgroundColor: isRequiredAgreed ? "#0097D8" : "#ccc",
            color: "#fff",
            border: "none",
            borderRadius: "12px",
            fontSize: "1rem",
            cursor: isRequiredAgreed ? "pointer" : "default"
          }}
        >
          
        </button>
      </div>

      {/* ===  === */}
      {modalType && (
        <div style={{
          position: "fixed",
          top: 0,
          left: 0,
          width: "100%",
          height: "100%",
          backgroundColor: "#fff",
          zIndex: 9999,
          display: "flex",
          flexDirection: "column"
        }}>
          {/*  */}
          <div style={{
            padding: "1rem",
            borderBottom: "1px solid #eee",
            display: "flex",
            justifyContent: "space-between",
            alignItems: "center"
          }}>
            <h3 style={{ margin: 0 }}>
              {{
                terms: "",
                privacy: " ",
                sensitive: "    "
              }[modalType]}
            </h3>
            <button onClick={() => setModalType(null)} style={{ fontSize: "1.2rem", background: "none", border: "none" }}></button>
          </div>

          {/*  */}
          <div style={{
            padding: "1rem",
            overflowY: "auto",
            flex: 1,
            fontSize: "0.95rem",
            lineHeight: "1.6",
            whiteSpace: "pre-wrap"
          }}>
            {contentMap[modalType]}
          </div>
        </div>
      )}
    </div>
  );
}

const checkboxStyle = {
  display: "flex",
  alignItems: "center",
  justifyContent: "space-between",
  margin: "1rem 0"
};

const viewBtn = {
  fontSize: "0.9rem",
  color: "#0097D8",
  marginLeft: "0.5rem",
  cursor: "pointer"
};

export default ConsentPage;
