import React, { useState } from "react";
import { useNavigate } from "react-router-dom";
import Header from "./Header";
import HamburgerMenu from "./HamburgerMenu";
import ValueIntro1 from "./assets/valueIntro1.png";
import ValueIntro2 from "./assets/valueIntro2.png";
import ValueIntro3 from "./assets/valueIntro3.png";

function ValueIntro() {
  const navigate = useNavigate();
  const [step, setStep] = useState(0);
  const [menuOpen, setMenuOpen] = useState(false);

  const handleNext = () => {
    if (step < pages.length - 1) {
      setStep(step + 1);
    } else {
      navigate("/survey");
    }
  };

  const skip = () => navigate("/survey");

  const pages = [
    {
      image: ValueIntro1,
      content: (
        <>
          <div style={bubbleStyle}>
            <p><strong style={{ color: "#1292EE" }}>?</strong><br />
               , ,   <br />
                  <br />
                .
            </p>
          </div>
          <div style={connector}></div>
          <div style={bubbleStyleSecondary}>
            <p>    <strong></strong>,<br />
                   <strong></strong>.
            </p>
          </div>
        </>
      ),
      layout: "center"
    },
    {
      image: ValueIntro2,
      content: (
        <div style={simpleText}>
            <br />
               .<br /><br />
                ,<br />
               <br />
            .
        </div>
      ),
      layout: "top"
    },
    {
      image: ValueIntro3,
      content: (
        <div style={simpleText}>
            <br />
            ,<br />
            .
        </div>
      ),
      layout: "top"
    }
  ];

  const current = pages[step];

  return (
    <div style={{
      display: "flex",
      flexDirection: "column",
      minHeight: "100vh",
      backgroundImage: `url(${current.image})`,
      backgroundSize: "cover",
      backgroundPosition: "top center",
      backgroundRepeat: "no-repeat",
      fontFamily: "sans-serif"
    }}>
      <Header title="   " setMenuOpen={setMenuOpen} />

      <div style={{
        flex: 1,
        display: "flex",
        flexDirection: "column",
        justifyContent: current.layout === "top" ? "flex-start" : "center",
        alignItems: "center",
        padding: "2rem 1rem",
        textAlign: "center"
      }}>
        {current.content}
      </div>

      <div style={{
        padding: "1rem",
        maxWidth: "500px",
        width: "100%",
        margin: "0 auto",
        display: "flex",
        justifyContent: step === 0 ? "space-between" : "center",
        gap: "1rem"
      }}>
        {step === 0 && (
          <button onClick={skip} style={whiteButton}></button>
        )}
        <button onClick={handleNext} style={blueButton}>
          {step < 2 ? "" : ""}
        </button>
      </div>

      <HamburgerMenu open={menuOpen} onClose={() => setMenuOpen(false)} navigate={navigate} />
    </div>
  );
}

const bubbleStyle = {
  backgroundColor: "#fff",
  border: "1px solid #1292EE",
  borderRadius: "20px",
  padding: "1rem",
  marginBottom: "1rem",
  fontSize: "0.95rem",
  color: "#333",
  lineHeight: "1.5",
  maxWidth: "320px"
};

const bubbleStyleSecondary = {
  backgroundColor: "#d9efff",
  borderRadius: "16px",
  padding: "1rem",
  fontSize: "0.95rem",
  color: "#333",
  lineHeight: "1.5",
  maxWidth: "320px"
};

const connector = {
  height: "16px",
  width: "2px",
  backgroundColor: "#1292EE",
  margin: "0 auto",
  marginBottom: "1rem"
};

const simpleText = {
  backgroundColor: "rgba(255, 255, 255, 0.8)",
  borderRadius: "16px",
  padding: "1.2rem",
  fontSize: "0.95rem",
  color: "#111",
  lineHeight: "1.5",
  maxWidth: "320px"
};

const whiteButton = {
  flex: 1,
  padding: "0.8rem 0",
  borderRadius: "12px",
  border: "1px solid #1292EE",
  backgroundColor: "white",
  color: "#1292EE",
  fontSize: "1rem",
  maxWidth: "220px"
};

const blueButton = {
  flex: 1,
  padding: "0.8rem 0",
  borderRadius: "12px",
  border: "none",
  backgroundColor: "#1292EE",
  color: "white",
  fontSize: "1rem",
  maxWidth: "220px"
};

export default ValueIntro;
